var _xyz = require('./xyz');

var _lab = require('./lab');

var hexcolor = "#007ace";

var deltae = require('./deltae.js');

var rgb = require('./rgb');

module.exports = {
  lab: function lab(XYZ, callback) {
    _lab(XYZ, callback);
  },
  xyz: function xyz(hexcolor, callback) {
    _xyz(hexcolor, callback);
  },
  hexcolor: function hexcolor(_hexcolor, callback) {
    rgb(_hexcolor, callback);
  },
  delta: function delta(color, compare, callback) {
    _lab(color, function (LabA) {
      _lab(compare, function (LabB) {
        deltae(LabA, LabB, callback);
      });
    });
  } //citas activas porx sem nombre, ap pat, apmat, fecnac,feccit, horari

};